import {
  IN_CURRENCY_OPT_EUR,
  IN_CURRENCY_OPT_GBP,
  IN_CURRENCY_OPT_USD,
  IN_CURRENCY_TYPE,
  IN_RATE_USD_EUR,
  IN_RATE_USD_GBP
} from "./constants";

export const convertCurrencyFromUsd = (i, value) => {
  return convertCurrency(
    value,
    IN_CURRENCY_OPT_USD,
    i[IN_CURRENCY_TYPE],
    Number(i[IN_RATE_USD_EUR]),
    Number(i[IN_RATE_USD_GBP])
  )
};

export const convertCurrency = (value, fromCurrency, toCurrency, rateUsdEur, rateUsdGbp) => {
  let result
  let rate = 1
  if (value) { // undefined | 0 | NaN
    switch (fromCurrency) {
      case IN_CURRENCY_OPT_USD:
        switch (toCurrency) {
          case IN_CURRENCY_OPT_EUR:
            rate = rateUsdEur
            break;
          case IN_CURRENCY_OPT_GBP:
            rate = rateUsdGbp
            break;
        }
        break
      case IN_CURRENCY_OPT_EUR:
        switch (toCurrency) {
          case IN_CURRENCY_OPT_USD:
            rate = 1 / rateUsdEur
            break;
          case IN_CURRENCY_OPT_GBP:
            rate = rateUsdGbp / rateUsdEur
            break;
        }
        break
      case IN_CURRENCY_OPT_GBP:
        switch (toCurrency) {
          case IN_CURRENCY_OPT_USD:
            rate = 1 / rateUsdGbp
            break;
          case IN_CURRENCY_OPT_EUR:
            rate = rateUsdEur / rateUsdGbp
            break;
        }
        break
    }
    result = value * rate
  } else {
    result = value
  }
  // console.log('convertCurrency (' + value + ' @ ' + rate + ') => (' + result + ') ' + fromCurrency + '=>' + toCurrency)
  return result
}



