import compute from './spreadsheet';

function onSubmit(event) {
  const formData = new FormData(this);
  const inputs = {};

  for (let key of formData.keys()) {
    if (key === 'currency') continue;

    inputs[key] = parseFloat(formData.get(key).replace(/[,$€£]/, ''));
  }

  const results = compute(inputs);

  for (let key of Object.keys(results)) {
    let input = document.querySelector(`[name="${key}"]`);
    const needsToBeAdded = !input;

    if (needsToBeAdded) {
      input = document.createElement('input');
      input.setAttribute('type', 'hidden');
    }

    input.setAttribute('name', key);
    input.value = results[key];

    if (needsToBeAdded) {
      this.appendChild(input);
    }
  }
}

window.addEventListener('DOMContentLoaded', function () {
  const form = document.querySelector('[data-js=ROICalculator]');
  form.addEventListener('submit', onSubmit);
});


