import * as c from './constants';
import * as xlsx from './xlsx';

const sumOutputs = (outputs, keys) => {
  return keys.reduce((p, c) => p + outputs[c], 0);
};

export default function (i) {
  const debug = false;
  const result = {};

  //table
  result[c.OUT_TACTICAL_SOURCING_1] = xlsx.rsD3(i, debug) * 1e6;
  result[c.OUT_TACTICAL_SOURCING_2] = xlsx.rsE3(i, debug) * 1e6;
  result[c.OUT_TACTICAL_SOURCING_3] = xlsx.rsF3(i, debug) * 1e6;

  result[c.OUT_PROGRAM_POLICY_COMP_1] = xlsx.rsD4(i, debug) * 1e6;
  result[c.OUT_PROGRAM_POLICY_COMP_2] = xlsx.rsE4(i, debug) * 1e6;
  result[c.OUT_PROGRAM_POLICY_COMP_3] = xlsx.rsF4(i, debug) * 1e6;

  result[c.OUT_VENDOR_MSTR_DT_MNG_1] = xlsx.rsD5(i, debug);
  result[c.OUT_VENDOR_MSTR_DT_MNG_2] = xlsx.rsE5(i, debug);
  result[c.OUT_VENDOR_MSTR_DT_MNG_3] = xlsx.rsF5(i, debug);

  result[c.OUT_PROCUREMENT_INV_RED_1] = xlsx.rsD6(i, debug) * 1e3;
  result[c.OUT_PROCUREMENT_INV_RED_2] = xlsx.rsE6(i, debug) * 1e3;
  result[c.OUT_PROCUREMENT_INV_RED_3] = xlsx.rsF6(i, debug) * 1e3;

  result[c.OUT_INVOICE_PAYMENT_PROC_IMP_1] = xlsx.rsD7(i, debug);
  result[c.OUT_INVOICE_PAYMENT_PROC_IMP_2] = xlsx.rsE7(i, debug);
  result[c.OUT_INVOICE_PAYMENT_PROC_IMP_3] = xlsx.rsF7(i, debug);

  result[c.OUT_ACH_CHECK_PAYMENT_RED_1] = xlsx.rsD8(i, debug);
  result[c.OUT_ACH_CHECK_PAYMENT_RED_2] = xlsx.rsE8(i, debug);
  result[c.OUT_ACH_CHECK_PAYMENT_RED_3] = xlsx.rsF8(i, debug);

  result[c.OUT_PROCESS_IMPROV_TOTAL_1] = sumOutputs(result, [
    c.OUT_VENDOR_MSTR_DT_MNG_1,
    c.OUT_PROCUREMENT_INV_RED_1,
    c.OUT_INVOICE_PAYMENT_PROC_IMP_1,
    c.OUT_ACH_CHECK_PAYMENT_RED_1,
  ]);

  result[c.OUT_PROCESS_IMPROV_TOTAL_2] = sumOutputs(result, [
    c.OUT_VENDOR_MSTR_DT_MNG_2,
    c.OUT_PROCUREMENT_INV_RED_2,
    c.OUT_INVOICE_PAYMENT_PROC_IMP_2,
    c.OUT_ACH_CHECK_PAYMENT_RED_2,
  ]);

  result[c.OUT_PROCESS_IMPROV_TOTAL_3] = sumOutputs(result, [
    c.OUT_VENDOR_MSTR_DT_MNG_3,
    c.OUT_PROCUREMENT_INV_RED_3,
    c.OUT_INVOICE_PAYMENT_PROC_IMP_3,
    c.OUT_ACH_CHECK_PAYMENT_RED_3,
  ]);

  result[c.OUT_REBATES_INCENTIVES_1] = (xlsx.rsD9(i, debug)) * 1e6;
  result[c.OUT_REBATES_INCENTIVES_2] = (xlsx.rsE9(i, debug)) * 1e6;
  result[c.OUT_REBATES_INCENTIVES_3] = (xlsx.rsF9(i, debug)) * 1e6;

  result[c.OUT_DAYS_PAY_OUT_EXT_1] = (xlsx.rsD10(i, debug)) * 1e6;
  result[c.OUT_DAYS_PAY_OUT_EXT_2] = (xlsx.rsE10(i, debug)) * 1e6;
  result[c.OUT_DAYS_PAY_OUT_EXT_3] = (xlsx.rsF10(i, debug)) * 1e6;

  result[c.OUT_WORKING_CAPITAL_TOTAL_1] = (xlsx.rsD12(i, debug)) * 10;
  result[c.OUT_WORKING_CAPITAL_TOTAL_2] = (xlsx.rsE12(i, debug)) * 10;
  result[c.OUT_WORKING_CAPITAL_TOTAL_3] = (xlsx.rsF12(i, debug)) * 10;

  //pie1
  result[c.OUT_VALUE_LEVERS_YEAR3] = result[c.OUT_WORKING_CAPITAL_TOTAL_3];
  result[c.OUT_VALUE_LEVERS_PERCENT_1] = 100 * result[c.OUT_TACTICAL_SOURCING_3] / result[c.OUT_WORKING_CAPITAL_TOTAL_3];
  result[c.OUT_VALUE_LEVERS_PERCENT_2] = 100 * result[c.OUT_PROGRAM_POLICY_COMP_3] / result[c.OUT_WORKING_CAPITAL_TOTAL_3];
  result[c.OUT_VALUE_LEVERS_PERCENT_3] = 100 * result[c.OUT_PROCESS_IMPROV_TOTAL_3] / result[c.OUT_WORKING_CAPITAL_TOTAL_3];
  result[c.OUT_VALUE_LEVERS_PERCENT_4] = 100 * (result[c.OUT_REBATES_INCENTIVES_3] + result[c.OUT_DAYS_PAY_OUT_EXT_3]) / result[c.OUT_WORKING_CAPITAL_TOTAL_3];

  //pie2
  result[c.OUT_TOTAL_SAVINGS_YEAR3] = sumOutputs(result, [
    c.OUT_WORKING_CAPITAL_TOTAL_1,
    c.OUT_WORKING_CAPITAL_TOTAL_2,
    c.OUT_WORKING_CAPITAL_TOTAL_3,
  ]);

  result[c.OUT_TOTAL_SAVINGS_PERCENT_1] = 100 * result[c.OUT_WORKING_CAPITAL_TOTAL_1] / result[c.OUT_TOTAL_SAVINGS_YEAR3];
  result[c.OUT_TOTAL_SAVINGS_PERCENT_2] = 100 * result[c.OUT_WORKING_CAPITAL_TOTAL_2] / result[c.OUT_TOTAL_SAVINGS_YEAR3];
  result[c.OUT_TOTAL_SAVINGS_PERCENT_3] = 100 * result[c.OUT_WORKING_CAPITAL_TOTAL_3] / result[c.OUT_TOTAL_SAVINGS_YEAR3];

  //bars section
  result[c.OUT_SAVINGS_RECOVERED] = (xlsx.wcF13(i, debug)) * 1e6;
  result[c.OUT_RETURNS_CURRENT] = (xlsx.wcF11(i, debug)) * 1e6;
  result[c.OUT_RETURNS_NEW] = (xlsx.wcF12(i, debug)) * 1e6;
  result[c.OUT_RETURNS_INCREASE_PERCENT] = (100 * xlsx.wcH5(i, debug));

  //rects section 1
  result[c.OUT_REDUCE_INVOICE_CURRENT] = (xlsx.piE34(i, debug)) * 1e3;
  result[c.OUT_REDUCE_INVOICE_NEW] = (xlsx.piE35(i, debug)) * 1e3;
  result[c.OUT_REDUCE_INVOICE_PERCENT] = (100 * (1 - result[c.OUT_REDUCE_INVOICE_NEW] / result[c.OUT_REDUCE_INVOICE_CURRENT]));

  //rects section 2
  result[c.OUT_REDUCE_CHECKS_CURRENT] = (xlsx.piE49(i, debug));
  result[c.OUT_REDUCE_CHECKS_NEW] = (xlsx.piF54(i, debug));
  result[c.OUT_REDUCE_CHECKS_PERCENT] = (100 * (1 - result[c.OUT_REDUCE_CHECKS_NEW] / result[c.OUT_REDUCE_CHECKS_CURRENT]));

  //rects section 3
  result[c.OUT_REDUCE_PROCUREMENT_CURRENT_PO] = (xlsx.piF20(i, debug));
  result[c.OUT_REDUCE_PROCUREMENT_NEW_PO] = (xlsx.piF23(i, debug));
  result[c.OUT_REDUCE_PROCUREMENT_PERCENT] = (100 * (1 - result[c.OUT_REDUCE_PROCUREMENT_NEW_PO] / result[c.OUT_REDUCE_PROCUREMENT_CURRENT_PO]));

  //rects section 4
  result[c.OUT_REDUCE_VENDOR_MNG_VALUE] = (xlsx.piF11(i, debug));
  result[c.OUT_REDUCE_VENDOR_MNG_PERCENT] = (100 * xlsx.piG4(i, debug));

  return result;
}



